@import url(https://fonts.googleapis.com/css2?family=Rozha+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxygen&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.line{height:1px;width:100%;background-color:#2180BB}

form{position:relative}form input[type="search"]{font-family:"Rozha One",serif;font-size:17px;border:1px solid #2180BB;height:43px;width:250px;border-radius:11px;padding-left:10px;border:1px solid #2180BB;border-radius:11px}form input[type="search"] ::-webkit-input-placeholder{color:#767676;opacity:1}form input[type="search"] :-moz-placeholder{color:#767676;opacity:1}form input[type="search"] ::-moz-placeholder{color:#767676;opacity:1}form input[type="search"] :-ms-input-placeholder{color:#767676;opacity:1}form .search-icon{font-size:22px;font-color:#454647;position:absolute;right:16px;top:9px;cursor:pointer}

h1{font-family:"Rozha One",serif;font-size:50px;font-weight:lighter}header{position:relative;display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:80px;margin:70px 0}header .container{width:100%;display:flex;flex-direction:row;justify-content:space-between;align-items:center}

.news-sort{display:flex;flex-direction:row;justify-content:space-between;align-items:center}.news-sort h2{font-size:30px;text-transform:uppercase}.sort-control{font-size:14px;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.sort-control select{cursor:pointer;font-size:13px;margin-left:10px;width:105px;padding:3px 8px;border:1px solid #2180BB;border-radius:7px}

.news-article .news-content{display:grid;grid-template-columns:1.25fr 1fr;grid-column-gap:70px;margin:70px 0}.news-article .news-content .article-img{height:450px}.news-article .news-content .article-img img{width:100%;height:100%;object-fit:cover}.article-details{display:flex;flex-direction:column;justify-content:space-between;align-items:flex-start}.article-details .title{font-size:20px;margin-bottom:30px}.article-details .link-date{display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:100%}.article-details .link-date .article-link{display:block}

*{padding:0;margin:0;box-sizing:border-box}body{background-color:#FDF6F0;color:#454647;font-family:"Oxygen",sans-serif;padding:0 85px}

